.Auth_container{
    width: 100vw;
    height: 100vh;
    position: absolute;
    z-index: 500;
    top: 0;
    right: 0;
    box-sizing: border-box;
    display: flex;
}
.Auth_container2{
    display: flex;
    position: absolute;
    right: 2.5rem;
    background-color: rgb(16,16,16);
    top: 1rem;
    text-align: center;
    flex-direction: column;
    border: 1px solid rgba(102,102,102,0.6);
    color: aliceblue;
    z-index: 500;
    padding: 0.5rem;
}
.User_Details{
    margin: 0 auto;
    display: flex;
    border-bottom: 2px solid grey;
    padding-inline-start: 1.2rem 0.9rem;
    padding: 0.7rem;
}
.email_Auth{
    font-size: larger;
    padding: 0.2rem 0.5rem;
    font-weight: bolder;
    margin: auto;
}
.btn_Auth{
    color: aliceblue;
    font-weight: bolder;
    font-size: larger;
    padding: 0.5rem 0.5rem;
    border: 0;
    margin: 0.2rem 0;
    background-color: transparent;
}
.btn_Auth:hover{
    background-color: rgb(47,47,47);
}
.btns_Auth{
    padding: 1.5rem 0;
    display: flex;
    flex-direction: column;
    text-decoration: none;
    cursor: pointer;
}
